@import url(https://fonts.googleapis.com/css?family=Rubik:400,500,700);
/*=====General Settings===== */
body {
  font-family: 'Rubik', sans-serif;
}
.heading {
  padding-top: 30px;
  color: palevioletred;
  font-size: 3rem;
}
ul {
  text-align: center;
  padding: 0;
}
li {
  display: inline-block;
  margin: 15px;
  font-size: 1.5rem;
}
a {
  text-decoration: none;
  color: white;
}
a:hover {
  text-decoration: none;
  cursor: pointer;
}

/*===== Font Size===== */
.x-large {
  font-size: 4rem;
}
.large {
  font-size: 3rem;
}
.medium {
  font-size: 2.5rem;
}
.small {
  font-size: 2rem;
}

/*===== Font Weight===== */
.bold {
  font-weight: 500;
}
.normal {
  font-weight: 400;
}

/*===== Font Color===== */
.blue-green {
  color: #04b2b1;
}
.dark-blue {
  color: #113865;
}
.pink {
  color: palevioletred;
}

.landing-container {
  z-index: 1;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.flexbox {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.introduction-container {
  max-width: 50%;
  padding-bottom: 10%;
}

@media screen and (min-width: 600px) and (max-width: 959px) {
  .introduction-container {
    max-width: 90%;
    padding-bottom: 20%;
  }
}

@media only screen and (max-width: 600px) {
  .introduction-container {
    max-width: 90%;
    padding-bottom: 20%;
  }
  .mobile-l {
    font-weight: 500;
    font-size: 3rem;
  }
  .mobile-m {
    font-weight: 400;
    font-size: 2rem;
  }
}

.contact-container {
  background: #113865;
  text-align: center;
  color: white;
}

span {
  font-size: 3em;
  padding: 5px;
}

header {
  z-index: 2;
  position: absolute;
  top: 0;
  right: 0;
  margin: 10px;
}
.menu-item {
  color: palevioletred;
}
.menu-item:hover {
  color: white;
}

.portfolio-container {
  background: #dcefef;
  text-align: center;
}

.portfolio-row {
  display: flex;
  flex-wrap: wrap;
}

.portfolio-column {
  padding: 10px;
  margin-bottom: 30px;
}

.project-container {
  height: 100%;
  padding: 20px 20px 10px;
  border: solid #04b2b1 1px;
  background: white;
}

.screenshot {
  width: 100%;
  max-width: 500px;
  height: 'auto';
  border: #113865 solid 2px;
}

.project-skills {
  margin-top: 15px;
}

.project-link-btn {
  background: #04b2b1;
  color: white;
  margin: 10px 15px 15px 0px;
  border: none;
  font-size: 2rem;
  padding: 10px;
}

.drawing {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -99;
}



